import * as vscode from 'vscode';
import { homedir } from 'os';

import { $fhc } from '../emmetCommon';
import { $mgc } from '../util';

export function activate(context: vscode.ExtensionContext) {
	context.subscriptions.push(vscode.commands.registerCommand('editor.emmet.action.updateImageSize', () => {
		return import('../updateImageSize').then(uis => uis.$jhc());
	}));

	$mgc(vscode.Uri.file(homedir()));
	$fhc(context);
}
